  transition: 0.3s ease;
}

.button--white {
  background: #fff;
  color: #408F95;
}

.button::after {
  content: "";
  border: 1px solid #408F95;
  position: absolute;
  background: #fff;
  color: #408F95;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -10;
}

.button--white::after {
  background-color: #408F95;
  border: 1px solid #fff;
}

.button::before {
  content: "";
  position: absolute;
  height: 3.375rem;
  width: 12.625rem;
  background-color: transparent;
  border: 1px solid #408F95;
  right: -0.25rem;
  bottom: -0.25rem;
  z-index: -100;
}

.button--white::before {
  border: 1px solid #fff;
}

.button span {
  display: inline-block;
  width: 2.5rem;
  height: 0.0625rem;
  background-color: #fff;
  z-index: 100;
  margin-left: 0.5rem;
  vertical-align: middle;
  position: relative;
  border-radius: 0.0625rem;
  transition: 0.3s ease;
  z-index: 100;
}

.button--white span {
  background-color: #408F95;
}

.button span::after {
  position: absolute;
  content: "";
  right: -0.0625rem;
  bottom: 0.21875rem;
  width: 0.625rem;
  height: 0.0625rem;
  background-color: #fff;
  transform: rotate(40deg);
  border-radius: 0.0625rem;
  transition: 0.3s ease;
  z-index: 100;
}

.button--white span::after {
  background-color: #408F95;
}

@media (hover: hover) {
  .button:hover {
    color: #408F95;
  }
  .button--white:hover {
    color: #fff;
  }
  .button:hover span {
    background-color: #408F95;
  }
  .button--white:hover span {
    background-color: #fff;
  }
  .button:hover span::after {
    background-color: #408F95;
  }
  .button--white:hover span::after {
    background-color: #fff;
  }
  .button:hover::after {
    transform: scale(1, 1);
  }
